body {
    padding: 50px;
}

p {
    cursor: pointer;
    margin: 0px;
}

.list-group-flush {
    border-radius: 5px;
}

.list-group-item {
    border-radius: 4px;
    align-items: center;
    display:flex;
    justify-content: space-between;
}

.complete {
    text-decoration: line-through;
}